import { CList } from "@components";
import { Col, Collapse, Row } from "antd";

const { Panel } = Collapse;
const CDetailAssetData = ({
  noInduk,
  tagNumber,
  noBarang,
  oldNumber,
  name,
  branch,
  location,
  flag,
  status,
  remark,
  model,
  masaManfaat,
  serialNumber,
  statusActiva,
  anggaran,
  value,
  vendor,
  qrCodeUrl,
  assetType,
  codeActiva,
  ownership,
  merk,
  tglPerolehan,
  currencyPerolehan,
  hargaPerolehan,
  paymentVoucher,
  isDisposal,
  disposalDescription,
}) => {
  return (
    <Row>
      <Col span={20} style={{ marginTop: 10 }}>
        <CList label="Nomor Induk " value={noInduk} color="#D3D3D3" />
        <CList label="Barcode" value={tagNumber} />
        <CList label="AMS NO" value={noBarang} color="#D3D3D3" />
        <CList label="REINS NO" value={oldNumber} />
        <CList label="Name" value={name} color="#D3D3D3" />
        <CList label="Branch" value={branch} />
        <CList label="Departement" value={vendor} color="#D3D3D3" />
        <CList label="Ownership" value={ownership} />
        <CList label="Code Activa" value={codeActiva} color="#D3D3D3" />
        <CList label="Location" value={location} />
        <CList label="Serial Number" value={serialNumber} color="#D3D3D3" />
        <CList label="Tanggal Perolehan" value={tglPerolehan} />
        <CList label="Jenis Barang" value={assetType} color="#D3D3D3" />
        <CList label="Kondisi Barang" value={status} />
        <CList label="Payment Voucher" value={paymentVoucher} color="#D3D3D3" />
        <CList label="Merek" value={merk} />
        <CList label="Model" value={model} color="#D3D3D3" />
        <CList
          label="Harga Perolehan"
          type={currencyPerolehan}
          value={hargaPerolehan}
        />
        <CList
          label="Currency Perolehan"
          value={currencyPerolehan}
          color="#D3D3D3"
        />
        <CList label="Anggaran Asset" value={anggaran} />
        <CList
          label="Massa Manfaat"
          value={masaManfaat === "" ? "0" : masaManfaat}
          color="#D3D3D3"
        />
        <CList label="Status Activa" value={statusActiva} />
        <CList label="Flag" value={flag} color="#D3D3D3" />
        <CList label="Value" value={value} />
        <CList label="Remark" value={remark} color="#D3D3D3" />
        {isDisposal && (
          <>
            <CList label="Disposal" value="Yes" />
            <CList
              label="Disposal Description"
              value={disposalDescription}
              color="#D3D3D3"
            />
          </>
        )}
      </Col>
      <Col span={4} style={{ marginTop: 10 }}>
        <Collapse style={{ backgroundColor: "#fff" }} bordered={false}>
          <Panel header="QR Code" key="1">
            <Col style={{ textAlign: "center" }}>
              <img width={150} src={qrCodeUrl} />
            </Col>
          </Panel>
        </Collapse>
      </Col>
    </Row>
  );
};

export default CDetailAssetData;
